<script>
  import EmojiDetails from "./EmojiDetails.svelte";
  import EmojiMenu from "./EmojiMenu.svelte";
  import EmojiMashup from "./EmojiMashup.svelte";

  let selectedEmoji = null;
  let components = [];

  let a = 0;
  function addComponent(c) {
    console.log("yo");
    components = [
      ...components,
      {
        svg: c,
        transform: {},
        a: a++,
      },
    ];
    console.log(components);
  }

  function onMove(c, dir) {
    const i = components.indexOf(c);
    console.log(c, i);
    if (i > -1) {
      const t = i + (dir === "up" ? -1 : 1);
      console.log(i, t);
      if (t < 0 || t >= components.length) return;

      const l = Math.min(t, i);
      const u = Math.max(t, i);
      const I = components[i];
      const T = components[t];

      console.log(l, u);

      components = [
        ...components.slice(0, l),
        ...(t > i ? [T, I] : [I, T]),
        ...components.slice(u + 1, components.length),
      ];
      console.log(components);
    }
  }

  function onDelete(c) {
    const i = components.indexOf(c);
    console.log(c, i);
    if (i > -1) {
      components = [
        ...components.slice(0, i),
        ...components.slice(i + 1, components.length),
      ];
    }
  }
</script>

